import React from "react";
import Header from "./ui/header";
import NavZone from "./ui/navZone";
import getUser from "@/api/getUser";
import { redirect } from "next/navigation";
export default async function Layout({
  children
}: {
  children: React.ReactNode;
}) {
  const {
    data
  } = await getUser();
  if (!data.user) {
    redirect("/login");
  }
  return <div className=" m-auto mt-12 flex h-full min-h-screen w-full flex-col overflow-x-hidden bg-bgcolor1 p-10" data-sentry-component="Layout" data-sentry-source-file="layout.tsx">
      <Header data-sentry-element="Header" data-sentry-source-file="layout.tsx" />
      <NavZone data-sentry-element="NavZone" data-sentry-source-file="layout.tsx">{children}</NavZone>
    </div>;
}